async function request(uri, method = 'GET', body = {}, headers = {}, external = false) {
  try {
    let resp = await fetch(
      external === false ? baseUrl + uri : uri,
      // Request with GET/HEAD method cannot have body.
      method === 'GET' || method === 'HEAD'
        ? {
          method: method,
          headers: headers,
        }
        : {
          method: method,
          headers: headers,
          body: body
        }
    );
    return resp.json();
  } catch (error) {
    throw new Error(error.toString());
  }
}

function formatNumber(number) {
  try {
    let decimalCount = Math.abs(0);
    decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

    const negativeSign = number < 0 ? "-" : "";

    let i = parseInt(number = Math.abs(Number(number) || 0).toFixed(decimalCount)).toString();
    let j = (i.length > 3) ? i.length % 3 : 0;

    return negativeSign + (j ? i.substr(0, j) + ',' : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + ',') + (decimalCount ? '.' + Math.abs(number - i).toFixed(decimalCount).slice(2) : "");
  } catch (e) {
    console.log(e)
  }
};

function formatMoney(amount, decimalCount = 2, decimal = ".", thousands = ",") {
  try {
    decimalCount = Math.abs(decimalCount);
    decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

    const negativeSign = amount < 0 ? "-" : "";

    let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
    let j = (i.length > 3) ? i.length % 3 : 0;

    return '₦' + negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : "");
  } catch (e) {
    console.log(e)
  }
};

function formatDate(date) {
  let d = new Date(date);
  return `${d.getDate()}/${d.getMonth() + 1}/${d.getFullYear()}`
}

function query(query) {
  return document.querySelectorAll(query);
}

function parseUrlQuery() {
  let queryMap = {};
  // get the query and remove the leading question mark character ?
  let query = window.location.search.replace('?', '');
  // split the query
  query.split('&').forEach(q => {
    // and convert it into an object
    let temp = q.split('=')
    queryMap[temp[0]] = temp[1];
  });

  return queryMap;
}


function alert(message, swatch = 'info', autohide = true) {
  let randID = Math.floor(Math.random() * 99999);
  let swatchColor = swatch;
  if (swatch === "error") {
    swatchColor = "danger";
  }
  let elem = `
    <div id='alert${randID}' style="" class="toast show">
      <div class="toast-body alert-${swatchColor}">
        <div class="d-flex">
          <strong class="me-auto title">${swatch.toString().toUpperCase()}</strong>
          <button type="button" class="btn-close" data-bs-dismiss="toast" aria-label="Close" onclick="document.getElementById('alert${randID}').outerHTML = '';" />
        </div>
        <hr class='my-2' />
        <span class='message'>${message}</span>
      </div>
    </div>`;

  // insert the element in the alert container
  let alertContainer = query('#alertContainer')[0];
  alertContainer.insertAdjacentHTML('beforeEnd', elem);

  if (autohide === true) {
    setTimeout(() => {
      let alert = query(`#alert${randID}`)[0];
      if (alert !== undefined) {
        alert.outerHTML = "";
      }
    }, 5000);
  }

}

function confirm(message, onConfirm = () => {}, onCancel = () => {}) {
  let randID = Math.floor(Math.random() * 99999);
  let elem = `
    <div id='confirm${randID}' style="" cl ass="toast show">
      <div class="rounded toast-body alert-light">
        <div class="d-flex">
          <strong class="title">${message}</strong>
        </div>
        <br cl ass='my-2' />
        <div class='d-flex justify-content-evenly w-100 mt-2'>
          <button id='confirm-cancel' class='btn btn-sm btn-danger w-100'>Cancel</button>
          <button id='confirm-confirm' class='btn btn-sm btn-success ms-2 w-100'>Confirm</button>
        </div>
      </div>
    </div>`;

  // insert the element in the confirm modal
  let confirmModal = query('#confirmModalContent')[0];
  confirmModal.innerHTML =  elem;

  query('#confirmModalTrigger')[0].click();

  function newOnCancel() {
    onCancel();
    query('#confirmModalDismissTrigger')[0].click();
  }

  function newOnConfirm() {
    onConfirm();
    query('#confirmModalDismissTrigger')[0].click();
  }

  query(`#confirm-cancel`)[0].addEventListener('click', newOnCancel, false);
  query(`#confirm-confirm`)[0].addEventListener('click', newOnConfirm, false);

}


function prompt(message, inputtype, onSubmit, onCancel) {
  // get the prompt
  var prompt = query('#prompt')[0];
  prompt.style.zIndex = "1070";
  var toast = new Toast(prompt);
  
  // set the message and the callback operations
  query('#prompt-message')[0].innerHTML = message;
  query('#prompt-type')[0].type = inputtype;
  query('#prompt-cancel')[0].addEventListener('click', onCancel, false);
  query('#prompt-prompt')[0].addEventListener('click', onSubmit, false);
  toast.show();
  prompt.addEventListener('hidden.bs.toast', function () {
    query('#prompt-cancel')[0].removeEventListener('click', onCancel);
    query('#prompt-submit')[0].removeEventListener('click', onSubmit);
    prompt.style.zIndex = "-1";
  });
}

function snackbar(title, message, swatch = 'alert-info') {
  // get the alert
  var snackbar = query('#snackbar')[0];
  // set the title, subtitle, swatch & message
  var notification = query('#notification')[0];
  notification.style.zIndex = "1070";
  query('#snackbar > div')[0].classList.add(swatch);
  query('#snackbar > div > div > .title')[0].innerHTML = title;
  query('#snackbar > div > .message')[0].innerHTML = message;
  var toast = new Toast(snackbar);
  toast.show();
  snackbar.addEventListener('hidden.bs.toast', function () {
    query('#snackbar > div')[0].classList.remove(swatch);
    notification.style.zIndex = "-1";
  });
}

export { baseUrl, request, formatMoney, formatNumber, formatDate, query, parseUrlQuery, alert, confirm, snackbar, API_FOOTBAL_APIKEY };